* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    @media #{$mediaExSmall} {
        max-width: 540px;
    }
    @media #{$mediaSmall} {
        max-width: 540px;
    }
    @media #{$mediaMedium} {
        max-width: 720px;
    }
    @media #{$mediaLarge} {
        max-width: 960px;
    }
    @media #{$mediaExLarge} {
        max-width: 1140px;
    }
}

body {
    font-family: 'Raleway', sans-serif;
}

p {
    font-size: 100%;
}

#titleParagraph {
font-size: 50px;
}

img {
    width: 100%;
    height: 100%
}
  
.tempHeader{
    display:flex;
    height: 100%;
    width: 100%; }
  
.landingRoutesColumn {
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center; }
  
  
.landingRoutesRow {
  display: flex;
  flex-direction: row;
  overflow-x: hidden;
  flex-wrap: nowrap;
  height: 100%;
  width: 100%;
  justify-content: left;
  flex-wrap: wrap;
}
  
.splash{
  height: 100%;
  width: 100%;
  max-height: 800px;

}
.landingPageButton {
  border-style: solid;
  border-color: #e6e6e6;
  background-color: #469477;
  border-width: thick;
  font-size: 50px;
  height: 100%;
  width: 100%;
  color: white;
}
  
.landingPageButton:hover {
  border-style: solid;
  border-color: #e6e6e6;
  background-color: #868687;
  border-width: thick;
  font-size: 50px;
  height: 100%;
  width: 100%;
  color: white;
  cursor: pointer;
}
  
  
.landingColumn1{
  display: flex;
  margin-top: 50px;
  width: calc(100% / 2);
  font-size: 25px;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-around;
  @media #{$mediaExSmall} {
    width: 100%;
  }
  @media #{$mediaSmall} {
    width: 100%;
  }
  @media #{$mediaMedium} {
    width: 100%;
  }
  @media #{$mediaLarge} {
    width: calc(100% / 2);
  }
}
  
.landingColumn2{
  display: flex;
  margin-top: 50px;
  width: calc(100% / 3);
  font-size: 25px;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-around;
  @media #{$mediaExSmall} {
    width: 100%;
  }
  @media #{$mediaSmall} {
    width: 100%;
  }
  @media #{$mediaMedium} {
    width: calc(100% / 2);
  }
  @media #{$mediaLarge} {
    width: calc(100% / 3);
  }
}
  
.landingColumnsContainer {
  display: flex;
  flex-direction: row;
  margin-top: 100px;
  flex-wrap: nowrap;
  justify-content: space-around;
  width: 100%;
  font-size: 25px;
}
  
.footerRow {
  display: flex;
  flex-direction: row;
  overflow-x: hidden;
  flex-wrap: nowrap;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  border-top: solid;
  border-color: "#ff637e";
}
